<template>
  <div class="share-dynamic-detail full-screen">
    <div class="page-panel">
      <pem-scroll-view ref="scroll"
        :pullup=true
        @pullUpLoad="loadMore"
        class="scroll-view"
        :probeType="3"
        :listenScroll="true">
        <div class="container"  v-show="!isInit">
          <div class="header-open" @click="goDownload">
            <img class="logo" src="http://m.pemarket.com.cn/static/logo.png" alt="">
            <h4 class="title">项目工场</h4>
            <p class="desc">找项目、找投资就到项目工场</p>
            <div class="btn">立即下载</div>
          </div>
          <div v-if="isDelete" class="header-delete">抱歉, 该动态信息已被作者删除~</div>
          <div v-if="!isDelete" class="dynamic-msgs">
            <div class="part1">
              <img class="avatar" :src="msgsObj.logo" alt=""  @error="defaultLogo">
              <h3 class="name">{{ msgsObj.name }}</h3>
              <p class="time">{{ msgsObj.createtime }}</p>
            </div>
            <div class="desc">{{ msgsObj.content }}</div>
            <!-- 动态图片 -->
            <div class="imgs-wrapper" v-if="imgsArr.length > 0">
              <img class="imgs-item" v-for="(item, index) in imgsArr" :key="index" :src="item" alt="">
            </div>
            <!-- 文字投票 -->
            <ul class="vote-txt-ul" v-if="voteTxtArr.length > 0">
              <li class="vote-txt-li" v-for="(item, index) in voteTxtArr" :key="index">
                <span class="label">{{ item.label }}、</span>{{ item.name }}
              </li>
            </ul>
            <!-- 图片投票 -->
            <ul class="vote-img-ul" v-if="voteImgArr.length > 0">
              <li class="vote-img-li" v-for="(item, index) in voteImgArr" :key="index" :class="{ 'vote-img-li-more': voteImgArr.length > 3 }">
                <img class="vote-img-option" :src="item.img" alt="">
                <div class="vote-img-txt clearfix">{{ item.label }}</div>
              </li>
            </ul>
            <div class="vote-count" v-if="voteCount">{{ voteCount }}人已投票</div>
            <ul class="topic-list" v-if="topicArr.length > 0">
              <li class="topic-item" v-for="(item, index) in topicArr" :key="index">{{ item }}</li>
            </ul>
            <ul class="count-list">
              <li class="count-item" @click="judgeGuide"><span class="count-like">{{ msgsObj.likecount }}</span></li>
              <li class="count-item" @click="judgeGuide"><span class="count-reply">{{ msgsObj.commentcount }}</span></li>
              <li class="count-item" @click="judgeGuide"><span class="count-share">{{ msgsObj.sharecount }}</span></li>
            </ul>
          </div>
          <!-- 底部评论 -->
          <div class="footer">
            <div class="title" v-if="commentArr.length > 0 && !isDelete">评论</div>
            <ul class="ul" v-if="commentArr.length > 0 && !isDelete">
              <li class="li" v-for="(item, index) in commentArr" :key="index">
                <img class="logo" :src="imgUrl + item.logo" alt=""  @error="defaultLogo">
                <div class="detail">
                  <div class="user">
                    <span>{{item.name}}</span>
                    <div class="love">
                      <span class="loveimg"></span>
                      <span>{{item.likecount}}</span>
                    </div>
                  </div> 
                  <div class="comment">
                    <span class="reply" v-if="item.parentName">回复{{item.parentName}}：</span> {{item.comment}}
                  </div>
                </div>
              </li>
            </ul>
            <div class="nocomment" v-if="commentArr.length == 0 || isDelete">咦，还有沙发，我来说几句，嘿...</div>
          </div>
        </div>
      </pem-scroll-view>
      <div class="guide-open-browser" v-if="showGuideMask" @click="closeMask">
        <div class="guide-img"></div>
      </div>
      <div class="footer-open" @click="judgeGuide">打开APP</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle, device } from 'common/js/util'
import tempUserLogo from './default-avatar.png'

export default {
  data () {
    return {
      showGuideMask: false,
      id: '',
      isInit: true,
      isDelete: false,
      imgUrl: ImgPath,
      msgsObj: {
        logo: tempUserLogo,
        name: '',
        createtime: '',
        content: '',
        likecount: null,
        commentcount: null,
        sharecount: null
      },
      imgsArr: [],
      topicArr: [],
      voteCount: null,
      voteTxtArr: [],
      voteImgArr: [],
      currentPage: 1,
      totalPage: 1,
      pageSize: '20',
      commentArr: []
    }
  },
  components: {
    PemScrollView
  },
  created () {
    setTitle('投融资信息对接服务平台')
    this.wxShare({})
    this._initData()
  },
  methods: {
    _initData () {
      if (!device.isWeixin() && device.isAndroid()) {
        window.location.href = `pemarket://www.pemarket.com.cn/attr?type=2&id=${this.$route.query.id}`
      }
      this.id = this.$route.query.id
      let option1 = {id: this.id}
      if (this.$route.query.fapp) option1['shareid'] = this.$route.query.fapp
      let option2 = {
        anonymous_id: this.id,
        currentPage: '1',
        pageSize: '10'
      }
      this._getAnonymousAppShareDetail(option1)
      this._getAnonymousAppShareCommentList(option2)
    },
    goDownload () {
      if (device.isPC()) {
        if (device.isIOS()) {
          window.location.href = 'https://itunes.apple.com/cn/app/id1057083949?mt=8'
        } else {
          window.location.href = 'http://a.app.qq.com/o/simple.jsp?pkgname=com.pemv2'
        }
        return false
      }
      this.$router.push('/download')
    },
    judgeGuide () {
      if (device.isWeixin()) {
        if (device.isIOS()) {
          window.location.href = `https://go.pemarket.com.cn/download-guide?type=2&id=${this.$route.query.id}`
        } else {
          this.showGuideMask = true
        }
      } else {
        this.$router.push('/download')
      }
    },
    closeMask () {
      this.showGuideMask = false
    },
    loadMore () {
      console.log('加载更多')
      if (this.currentPage >= this.totalPage) return
      this.currentPage = this.currentPage * 1 + 1
      let options = {
        anonymous_id: this.$route.query.id,
        currentPage: this.currentPage,
        pageSize: '10'
      }
      this._getAnonymousAppShareCommentList(options)
    },
    defaultLogo (e) {
      e.target.src = tempUserLogo
    },
    _getAnonymousAppShareDetail (options) {
      MiniSiteApi.getAnonymousAppShareDetail(options).then(res => {
        this.isInit = false
        if (+res.resultcode === 1) {
          if (res.logo) {
            this.msgsObj.logo = this.imgUrl + res.logo
          }
          this.msgsObj.name = res.name || ''
          this.msgsObj.createtime = res.createtime || ''
          this.msgsObj.content = res.content || ''
          this.msgsObj.likecount = res.likecount || '0'
          this.msgsObj.commentcount = res.commentcount || '0'
          this.msgsObj.sharecount = res.sharecount || '0'
          if (res.image && res.image.length > 0) {
            this.imgsArr = res.image.map(item => {
              item = this.imgUrl + item
              return item
            })
          }
          if (res.topicList && res.topicList.length > 0) {
            this.topicArr = res.topicList.map(item => {
              return (item.name || '')
            })
          }
          if (res.vote && res.vote.type) {
            let tempObj = res.vote
            this.voteCount = tempObj.vote_count
            if (tempObj.type === 'txt') {
              this.voteTxtArr = tempObj.option || []
            } else {
              this.voteImgArr = (tempObj.option || []).map(item => {
                item.img = this.imgUrl + item.img
                return item
              })
            }
          }
          this.wxShare({
            title: res.share_title || null,
            content: res.share_desc || null,
            imgUrl: res.share_icon ? (this.imgUrl + res.share_icon) : null
          })
        } else if (+res.resultcode === 2) {
          this.isDelete = true // 删除或者禁用
        }
      }).catch((error) => {
        console.error(error)
        // this.$router.push('/invalid')
      })
    },
    _getAnonymousAppShareCommentList (options) {
      MiniSiteApi.getAnonymousAppShareCommentList(options).then(res => {
        this.isInit = false
        this.commentArr = res && res.resultList && res.resultList.length > 0 ? res.resultList : []
      }).catch((error) => {
        console.error(error)
        // this.$router.push('/invalid')
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .share-dynamic-detail
    min-width: 300px
  .page-panel
    top: 0
  .guide-open-browser
    position: fixed
    left: 0
    top: 0
    right: 0
    bottom: 0
    z-index: 1000
    opacity: 0.7
    background: #000000
    .guide-img
      width: 5.12rem
      height: 2.54rem
      margin: 0.48rem auto 0
      bg-image('./bg_guide')
      background-size: 100% 100%
      background-repeat: no-repeat
  .header-open
    position: relative
    height: 60px
    border-bottom: 0.5px solid #f3f3f3
    background: #ffffff
    .logo
      position: absolute 
      left: 23px
      top: 10px
      height: 40px
      width: 40px
      border-radius: 4px;
    .title
      position: absolute 
      left: 71px
      top: 8px
      font-family: PingFangSC-Medium
      font-size: 16px
      font-weight: bold
      color: #41403E
      line-height: 22px
    .desc
      position: absolute 
      left: 71px
      bottom: 10px
      font-family: PingFangSC-Regular
      font-size: 13px
      color: #8B8B8B
      line-height: 18px
    .btn
      position: absolute
      top: 16px
      right: 11px
      width: 78px
      height: 30px
      line-height: 30px
      background: #FFC131
      border-radius: 2px
      font-family: PingFangSC-Regular
      font-size: 15px
      color: #41403E
      letter-spacing: -0.3px
      text-align: center
  .footer-open
    position: fixed
    left: 50%
    bottom: 25px
    margin-left: -79px;
    width: 158px
    padding: 11px 0
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    line-height: 22px
    text-align: center
    background: #FFC131;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.13);
    border-radius: 22px;
  .container
    max-width: 750px;
    margin: 0px auto;
    padding-bottom: 80px
    .header-delete
      text-align: center;
      background-color: #ffffff
      color: #a7abb2;
      font-size: 15px
      padding: 120px 10px
      border-bottom: 8px solid #F2F2F2
    .dynamic-msgs
      padding: 0 15px 22px
      border-bottom: 8px solid #F2F2F2
      .part1
        position: relative
        height: 71px
        .avatar
          position: absolute
          left: 0
          top: 15px
          width: 44px
          height: 44px
          border-radius: 100%
        .name
          position: absolute
          left: 52px
          top: 16px
          font-family: PingFangSC-Medium;
          font-size: 15px;
          font-weight: bold
          color: #333333;
          line-height: 21px
        .time
          position: absolute
          left: 52px
          bottom: 13px
          font-family: PingFangSC-Regular;
          font-size: 12px;
          color: #A7A7A7;
          line-height: 17px
      .desc
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #333333;
        line-height: 22px;
      .imgs-wrapper
        font-size: 0
        .imgs-item
          width: 2rem
          height: 2rem
          margin: 15px 0.3rem 0 0
          &:nth-child(3n + 0)
            margin-right: 0
      .vote-txt-ul
        padding-top: 4px
        .vote-txt-li
          position: relative
          margin-top: 12px
          padding: 9px 52px 9px 38px
          border: 1px solid #EEEEEE;
          border-radius: 4px
          font-size: 14px;
          color: #333333;
          line-height: 20px
          .label
            position: absolute
            left: 14px
            top: 9px
          .rate
            position: absolute
            right: 15px
            top: 9px
            font-size: 12px;
            color: #999999;
            line-height: normal
      .vote-img-ul
        font-size: 0
        .vote-img-li
          position: relative
          display: inline-block
          margin: 15px 0.3rem 0 0
          width: 2rem
          padding-top: 2rem
          &.vote-img-li-more
            width: 2.5rem
            padding-top: 2.5rem
            .vote-img-option
              width: 2.5rem
              height: 2.5rem
          .vote-img-option
            position: absolute
            top: 0
            left: 0
            width: 2rem
            height: 2rem
            border-radius: 2px;
          .vote-img-txt
            margin-top: 8px
            padding: 0 5px 0 9px
            border: 0.5px solid #EEEEEE;
            border-radius: 2px;
            height: 24px
            line-height: 24px
            font-size: 15px;
            color: #333333
            .rate
              font-size: 11px;
              color: #999999;
      .vote-count
        font-size: 11px;
        color: #999999;
        line-height: 16px
        margin-top: 8px
      .topic-list
        font-size: 0
        padding: 20px 0 0
        .topic-item
          display: inline-block
          height: 21px
          line-height: 21px
          padding: 0 6px 0 21px
          margin: 0 12px 8px 0
          font-size: 12px;
          color: #333333;
          background: #F2F3F2;
          border-radius: 10px;
          bg-image('dynamic-huati')
          background-size: 14px 14px
          background-repeat: no-repeat
          background-position: 5px center
          &:last-child
            margin-right: 0
      .count-list
        overflow: hidden
        text-align: center
        margin-top: 20px;
        .count-item
          width: 33.33%
          float: left
          font-size: 12px;
          color: #666666;
          .count-like, .count-reply, .count-share
            display: inline-block
            padding-left: 21px
            line-height: 17px
            background-size: 14px 14px
            background-repeat: no-repeat
            background-position: left center
          .count-like
            bg-image('dynamic-zan')
          .count-reply
            bg-image('dynamic-reply')
          .count-share
            bg-image('dynamic-share')
    .footer
      padding: 0 15px
      .title
        padding: 14px 0 6px 0
        font-size: 14px
        font-weight: bold
        color: #41403E
      .nocomment
        text-align: center
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #41403E;
        line-height: 20px
        padding: 162px 0
        bg-image('dynamic-comment-empty')
        background-size: 100px 100px
        background-repeat: no-repeat
        background-position: center 42px
      .li
        position: relative
        border-bottom: 0.5px solid #EFEFEF
        padding: 10px 0 10px 47px
        &:last-of-type
          border-bottom: none
        .logo
          position: absolute
          left: 0
          top: 10px
          width: 35px
          height: 35px
          border-radius: 100%
        .detail
          .user
            height: 18px
            line-height: 18px
            font-size: 13px
            color: #A1A1A1
            position: relative
            .love
              display: inline-block
              position: absolute
              height: 18px
              line-height: 18px
              right: 0
              font-size: 11px 
              .loveimg
                width: 13px
                height: 13px
                bg-image('icon_pingl_zan')
                background-size: 100% 
                background-repeat: no-repeat
              span
                vertical-align: middle
                display: inline-block 
          .comment
            padding-top: 6px
            font-size: 14px
            line-height: 20px 
            color: #353535
            .reply
              color: #A1A1A1
</style>



